function getRandomInt(min, max) {
  return Math.round(Math.random() * (max - min) + min)
}

export function shuffle(arr) {
  let _arr = arr.slice(0)
  for (let i = 0; i < _arr.length; i++) {
    let j = getRandomInt(0, i)
    let temp = _arr[i]
    _arr[i] = _arr[j]
    _arr[j] = temp
  }
  return _arr
}

export function debounce(fn, delay) {
  let timer
  return function (...args) {
    if (timer) {
      clearTimeout(timer)
    }
    timer = setTimeout(() => {
      fn.apply(this, args)
    }, delay)
  }
}

export function dateFormat (timestamp, fmt) {
  let D = new Date()
  let week = ''
  timestamp && D.setTime(timestamp)
  fmt = fmt || 'yyyy-mm-dd'
  let d = {
    'm+': D.getMonth() + 1,
    'd+': D.getDate(),
    'w+': week.charAt(D.getDay()),
    'h+': D.getHours(),
    'M+': D.getMinutes(),
    's+': D.getSeconds()
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (D.getFullYear() + '').slice(-RegExp.$1.length))
  }
  Object.keys(d).forEach((key) => {
    if (new RegExp(`(${key})`).test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? d[key] : (`00${d[key]}`).slice(('' + d[key]).length))
    }
  })
  return fmt
}



