import Vue from 'vue'
import Router from 'vue-router'
const Home = (resolve) => require(['../components/home/index'], resolve)
const RecommendSongList = (resolve) => require(['../components/recommend/song-list'], resolve)
const Disc = (resolve) => require(['../components/disc/disc'], resolve)
const singer = (resolve) => require(['../components/singer/singer'], resolve)
const singerDetail = (resolve) => require(['../components/singer-detail/singer-detail'], resolve)
const rank = (resolve) => require(['../components/rank/rank'], resolve)
const topList = (resolve) => require(['../components/top-list/top-list'], resolve)
const search = (resolve) => require(['../components/search/search'], resolve)
const user = (resolve) => require(['../components/user/user'], resolve)
const Login = (resolve) => require(['../components/login/index'], resolve)
const Collection = (resolve) => require(['../components/user/collection-list'], resolve)
const Discussion = (resolve) => require(['../components/user/discussion-list'], resolve)
const FoucusList = (resolve) => require(['../components/user/focus-list'], resolve)
const LikeList = (resolve) => require(['../components/user/like-list'], resolve)
const SongDiscuss = (resolve) => require(['../components/discussion/index'], resolve)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/disc',
      name: 'Disc',
      component: Disc
    },
    {
      path: '/recommend-song-list',
      name: 'RecommendSongList',
      component: RecommendSongList
    },
    {
      path: '/singer',
      name: 'singer',
      component: singer,
      children: [
        {
          path: ':id',
          component: singerDetail
        }
      ]
    },
    {
      path: '/rank',
      name: 'rank',
      component: rank,
      children: [
        {
          path: ':id',
          component: topList
        }
      ]
    },
    {
      path: '/song-discuss',
      name: 'SongDiscuss',
      component: SongDiscuss
    },
    {
      path: '/search',
      name: 'search',
      component: search,
      children: [
        {
          path: ':id',
          component: singerDetail
        }
      ]
    },
    {
      path: '/user',
      name: 'user',
      component: user
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/collection',
      name: 'collection',
      component: Collection
    },
    {
      path: '/discussion',
      name: 'Discussion',
      component: Discussion
    },
    {
      path: '/focus-list',
      name: 'FoucusList',
      component: FoucusList
    },
    {
      path: '/like-list',
      name: 'LikeList',
      component: LikeList
    },
    {
      path: '*',
      redirect: '/home'
    }
  ]
})



