<template>
  <div class="header-bar-wrap">
    <div class="brand-bar">
      <span class="brand"></span>
      <h2 class="name">BB Music</h2>
      <i class="icon icon-mine" @click="handleMyPage"></i>
      <router-link tag="i"  :to="{name: 'user'}"></router-link>
    </div>
    <nav class="nav">
      <router-link class="item" :to="{name: 'home'}">
        <span class="text"></span>
      </router-link>
      <router-link class="item" :to="{name: 'search'}">
        <span class="text"></span>
      </router-link>
    </nav>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    methods: {
      handleMyPage() {
        if (this.islogin) {
          this.$router.push('/user')
        } else {
          this.$router.push('/login')
        }
      }
    },
    computed: {
      ...mapGetters(['islogin'])
    }
  }
</script>

<style scoped lang="scss">
  @import "../../common/scss/variable";
  @import "../../common/scss/mixin";

  .header-bar-wrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 88px;
    .brand-bar {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 44px;
      color: $color-theme;
      border-bottom: 1px solid #f5f5f5;
      .brand {
        width: 30px;
        height: 32px;
        margin-right: 9px;
        @include bg("./logo");
      }
      .name {
        font-size: 18px;
      }
      .icon {
        position: absolute;
        right: 0;
        top: 0;
        padding: 12px;
        font-size: 20px;
      }
    }
    .nav {
      display: flex;
      align-items: center;
      height: 44px;
      .item {
        flex: 1;
        line-height: 44px;
        text-align: center;
        font-size: 16px;
        color: $color-text-l;
        &.router-link-active {
          color: $color-sub-theme;
          .text {
            border-bottom-color: $color-sub-theme;
          }
        }
        .text {
          padding-bottom: 6px;
          border-bottom: 3px solid transparent;
        }
      }
    }
  }
</style>



